@media screen and ( min-width: map-get( $bp-mobile-first, md ) ) {

  .l-balance-mutation {

    &__inner {
      flex: none;
      flex-direction: row;
      align-items: flex-start;
      justify-content: space-between;
    }//&__inner

    .balance-mutation-panel {
      margin-left: 29px;
      min-width: 0;

      &__body {
        background: none;
        min-width: 0;

        .mutation-table-desktop {
          display: flex;
          flex-direction: column;

          border-radius: 5px;
          box-shadow: 0 4px 3px 0 rgba(0, 0, 0, 0.05), 0 1px 4px 0 rgba(0, 0, 0, 0.11);
          background-color: #ffffff;

          &__title {
            margin: 21px 0 0 30px;

            font-size: 16px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: 0.4px;
            color: map-get($colors, charcoal-grey);
          }//.title


          .biq-col {
            $margin-right: 5;
            min-width: 0;
            margin-right: 5px;

            &--spacer-start {
              flex: 30 1 0;
            }//&--spacer-start

            &--date {
              flex: ( 170 - $margin-right ) 1 0;
            }//&--date

            &--description {
              flex: ( 284 - 10 ) 1 0;
              margin-right: 10px;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }//&--description

            $design-diff: 15;
            &--amount{
              flex: ( 134 - $design-diff - $margin-right ) 1 0;
            }//&--amount

            &--balance {
              flex: ( 105 + $design-diff - $margin-right ) 1 0;
            }//&--balance

          }//.biq-col

          &__header {
            display: flex;
            align-items: center;
            height: 58px;
            margin-top: 20px;
            background-color: map-get($colors, light-grey-7);
            border-top: 0.5px solid map-get( $colors, gray-87 );
            border-bottom: 0.5px solid map-get( $colors, gray-87 );

            font-size: 13px;
            font-weight: 600;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: 0.3px;
            color: map-get($colors, charcoal-grey);
          }//&__header

          &__body {
            position:relative;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            min-height: 300px;

            .biq-row {
              display: flex;
              align-items: center;
              height: 45px;
              border-bottom: 0.5px solid map-get( $colors, gray-87 );

              font-size: 13px;
              font-weight: normal;
              font-style: normal;
              font-stretch: normal;
              line-height: normal;
              letter-spacing: 0.3px;
              color: map-get($colors, charcoal-grey);
            }//.biq-row

            .c-loading-indicator {
              background: rgba( #FFFFFF, 0.8 );
            }//.c-loading-indicator

          }//&__body

          &__footer {
            display: flex;
            align-items: flex-start;
            justify-content: center;
            height: 75px;

            .pagination {
              display: flex;
              margin-top: 20px;

              &__item {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 25px;
                height: 25px;
                margin-right: 10px;
                border-radius: 13px;
                border: 1px solid map-get( $colors, very-light-pink-five );
                transition: all 250ms ease-in-out;

                font-size: 13px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: 0.3px;
                color: map-get($colors, charcoal-grey);

                &.is-active {
                  background-color: map-get($colors, dark-aqua);

                  font-weight: bold;
                  color: #ffffff;
                }//&.is-active

                &--nav-first {
                  background: url('../../../images/icons/double-next.svg') no-repeat center;
                  background-size: contain;
                  transform: rotate( 180deg );
                  transform-origin: center;
                  border: none;

                  &:hover {
                    transform: rotate( 180deg ) scale( 1.14 );
                  }//&:hover

                }//&--nav-first

                &--nav-prev {
                  background: url('../../../images/icons/singlenext.svg') no-repeat center;
                  background-size: contain;
                  transform: rotate( 180deg );
                  transform-origin: center;
                  border: none;

                  &:hover {
                    transform: rotate( 180deg ) scale( 1.14 );
                  }//&:hover

                }//&--nav-prev

                &--nav-next {
                  background: url('../../../images/icons/singlenext.svg') no-repeat center;
                  background-size: contain;
                  border: none;

                  &:hover {
                    transform: scale( 1.14 );
                  }//&:hover

                }//&--nav-next

                &--nav-last {
                  margin-right: 0;
                  background: url('../../../images/icons/double-next.svg') no-repeat center;
                  background-size: contain;
                  border: none;

                  &:hover {
                    transform: scale( 1.14 );
                  }//&:hover

                }//&--nav-last

              }//&__item

            }//.pagination

          }//&__footer

        }//.mutation-record
      }//&__body

    }//.balance-mutation-panel

  }

}