html, body, #root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #e3eaec;
  font-family: 'NunitoSans',sans-serif; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.main-wrapper {
  min-height: 100%;
  padding-top: 50px !important; }
  @media screen and (min-width: 768px) {
    .main-wrapper {
      min-height: 0;
      padding-bottom: 20px !important; } }
  @media screen and (max-width: 767px) {
    .main-wrapper--mobile-no-header {
      padding-top: 0 !important; } }

.biq-wrapper {
  flex: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto; }
  .biq-wrapper--sm-side-padding {
    padding: 0 15px; }
  @media screen and (min-width: 768px) {
    .biq-wrapper {
      padding: 0 64px; }
      .biq-wrapper--sm-side-padding {
        padding: 0 64px; }
      .biq-wrapper--md-narrow-side-padding {
        padding: 0 5px; }
      .biq-wrapper--md-no-side-padding {
        padding: 0; } }
  @media screen and (min-width: 992px) {
    .biq-wrapper {
      padding: 0; }
      .biq-wrapper--sm-side-padding {
        padding: 0; } }
  .biq-wrapper__inner {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    box-sizing: border-box; }
    @media screen and (min-width: 992px) {
      .biq-wrapper__inner {
        padding: 0 0;
        width: 1000px; } }

input[type="text"], input[type="password"], input[type="number"] {
  outline: none !important;
  appearance: none;
  border: 0; }
  input[type="text"]:disabled, input[type="password"]:disabled, input[type="number"]:disabled {
    background-color: transparent; }
  input[type="text"].has-value::placeholder, input[type="password"].has-value::placeholder, input[type="number"].has-value::placeholder {
    color: transparent; }

input[type="number"].no-spinner {
  -moz-appearance: textfield; }
  input[type="number"].no-spinner::-webkit-inner-spin-button, input[type="number"].no-spinner::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0; }

.custom-card-styles {
  border-radius: 0px !important;
  box-shadow: 0 4px 3px 0 rgba(0, 0, 0, 0.05), 0 1px 4px 0 rgba(0, 0, 0, 0.11) !important; }

.container-wrapper {
  display: flex;
  justify-content: center; }

.container-inner {
  max-width: 1000px;
  width: 100%; }

.layout-center {
  display: flex;
  align-items: center;
  justify-content: center; }

.mr-0 {
  margin: 0px; }

.hide {
  display: none; }

.disabled {
  opacity: 0.5;
  pointer-events: none; }

/*
===========================================
      Basic Themeing for Sibisnis
===========================================
*/
.sibisnis-card {
  background-color: #ffffff;
  box-shadow: 0 4px 3px 0 rgba(0, 0, 0, 0.05), 0 1px 4px 0 rgba(0, 0, 0, 0.11) !important; }

.sibisnis-container-wrapper {
  display: flex;
  justify-content: center; }

.sibisnis-container-inner {
  max-width: 1000px;
  width: 100%;
  padding: 0px 10px; }

/* icon touch area*/
.icon-touch-area-container-50 {
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer; }

.icon-touch-area-container-40 {
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer; }

/* Ripple effect */
.ripple {
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0); }

.ripple:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  background-image: radial-gradient(circle, #a2a0a0 10%, transparent 10.01%);
  background-repeat: no-repeat;
  background-position: 50%;
  transform: scale(10, 10);
  opacity: 0;
  transition: transform .5s, opacity 1s; }

.ripple:active:after {
  transform: scale(0, 0);
  opacity: .3;
  transition: 0s; }

/* Ripple effect */
.ripple-mobile {
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0); }

.ripple-mobile:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  background: radial-gradient(circle, #a2a0a0 10%, transparent 10.01%) no-repeat 50%;
  transform: scale(10, 10);
  opacity: 0;
  transition: transform .5s, opacity 1s; }

.ripple-mobile:active:after {
  transform: scale(0, 0);
  opacity: .3;
  transition: 0s; }

